import React, { useEffect, useState } from "react";

import Button from "@mui/material/Button";
import { Box } from "@mui/material";
import Notificationtable from "./Notificationtable";

const Notificationtab = () => {
  const [notifications, setNotifications] = useState([]);
  const API_BASE_URL =
    window._env_?.REACT_APP_API_BASE_URL || "http://localhost:8080";

  useEffect(() => {
    readNotifications();
  }, []);

  const readNotifications = async () => {
    try {
      const res = await fetch(`${API_BASE_URL}/readNotifications`, {
        method: "POST",
      });
      let data = await res.json();
      setNotifications(data);
      console.log(data);
    } catch (err) {
      console.log("Error reading from TaskLists: ", err);
    }
  };
  return (
    <div className="m-5 mt-10">
      {/* <Box display="flex" justifyContent="flex-end" mb={2}>
        <Button variant="contained" color="success" onClick={readNotifications}>
          Read Tasklist
        </Button>
      </Box> */}
      <Notificationtable notifications={notifications} />
    </div>
  );
};

export default Notificationtab;
