import { Button, Collapse } from "@mui/material";
import { useState } from "react";
import Rangechangebox from "./Rangechangebox";
import Rangetable from "./Rangetable";
import { RangeTabProps } from "./types";

const Rangetab = ({ ranges }: RangeTabProps) => {
  const [collapsed, setCollapsed] = useState<boolean>(false);
  const rangeManageClicked = () => {
    setCollapsed((prev) => !prev);
  };
  return (
    <div>
      <div className="mt-8 mb-8">
        <Button
          variant="outlined"
          color="secondary"
          onClick={rangeManageClicked}
        >
          Manage Range
        </Button>
        <Collapse in={collapsed} timeout="auto" unmountOnExit collapsedSize={0}>
          <Rangechangebox />
        </Collapse>
      </div>
      <Rangetable ranges={ranges} />
    </div>
  );
};

export default Rangetab;
