import { useAuth } from "../context/AuthContext";

const Header = () => {
  const { setIsAuthenticated } = useAuth();
  const logOut = () => {
    setIsAuthenticated(false);
  };
  return (
    <header className="p-4 text-black shadow-md shadow-gray-300 h-20 flex items-center justify-between lg:p-10">
      <img src="/icon.png" alt="logo" className="h-8 w-8" />
      <button
        className="bg-buttonColor text-white font-semibold py-2 px-6 rounded-lg hover:bg-blue-400 transition"
        onClick={() => logOut()}
      >
        {useAuth().isAuthenticated ? "Log out" : "Log in"}
      </button>
    </header>
  );
};

export default Header;
